import { getUserInfo, setUserInfo, removeUserInfo } from "@/utils/setlocal";
export default {
  namespaced: true,
  state() {
    return {
      userinfo: {
        token: getUserInfo(),
      },
    };
  },
  mutations: {
    setuserinfo(state, data) {
      state.userinfo = data;
      setUserInfo(data);
    },
    clearUserInfo(state) {
      state.userinfo = { token: "" };
      removeUserInfo();
    },
  },
  getters: {},
  actions: {},
};
