      // const musicid = localStorage.getItem("musicnamefirst");
      try {
        const res = await getmusicpalyer2({
          musicid: loveList.value[0]
        });
        musicstat.value = res.data.url;
      } catch {
        ElMessage.error("获取音乐失败");
        isplayer.value = true;
        isload.value = true;
      }
    };

    // 展示和隐藏侧边栏
    const showbtnsty = ref(null);
    const siderbarshow = ref(null);
    const originalWidth = ref("20vw");
    const siderbarwid = ref(null);
    const togglebtn = () => {
      if (siderbarshow.value.style.width === "0px") {
        siderbarwid.value.style.width = originalWidth.value;
        siderbarshow.value.style.width = originalWidth.value;
        showbtnsty.value.innerHTML = "《";
        siderbarshow.value.style.display = "block";
      } else {
        siderbarwid.value.style.width = "0";
        siderbarshow.value.style.width = "0";
        showbtnsty.value.innerHTML = "》";
        siderbarshow.value.style.display = "none";
      }
    };

    // 播放模式状态 (0: 单曲循环, 1: 顺序播放, 2: 随机播放)
    const playMode = ref(0);
    // 计算播放模式对应的图片
    const playModeImage = computed(() => {
      const modes = [require("@/assets/images/单曲循环播放.png"), require("@/assets/images/顺序播放.png"), require("@/assets/images/随机播放.png")];
      return modes[playMode.value];
    });

    // 切换播放模式
    const changePlayMode = () => {
      playMode.value = (playMode.value + 1) % 3;
      if (playMode.value === 0) {
        alertstitle.value?.showAlert("单曲循环", 500);
      } else if (playMode.value === 1) {
        alertstitle.value?.showAlert("顺序播放", 500);
      } else if (playMode.value === 2) {
        alertstitle.value?.showAlert("随机播放", 500);
      }
    };

    // 获取喜欢的列表
    const loveList = computed(() => store.state.love.loveList);
    onMounted(async () => {
      // 确保 loveList 在使用前已经被初始化
      await store.dispatch("love/fetchLoveList");
      // console.log(loveList.value, 1111111111111);
    });

    // 当前播放索引
    const currentIndex = ref(0);
    const nowpalynum = ref(0);
    // 播放指定索引的歌曲
    const playSongByIndex = async index => {
      if (index < 0 || index >= loveList.value.length) return;
      nowpalynum.value = index;
      const songName = loveList.value[index];
      currentIndex.value = index;
      try {
        loading.value = true;
        const res = await getmusicpalyer2({
          musicid: songName
        });
