      switch (playMode.value) {
        case 0:
          // 单曲循环
          nextIndex = (currentIndex.value + 1) % loveList.value.length;
          break;
        case 1:
          // 顺序播放
          nextIndex = (currentIndex.value + 1) % loveList.value.length;
          // console.log(nextIndex, "我是下一首");
          break;
