          if (nextIndex === currentIndex.value && loveList.value.length > 1) {
            nextIndex = (nextIndex + 1) % loveList.value.length;
          }
          break;
      }
      playSongByIndex(nextIndex);
      setTimeout(() => {
        isThrottled.value = false;
        loading.value = false;
      }, 1000);
    };

    // 下一首逻辑
