    const prevSong = () => {
      if (isThrottled.value || loading.value) return;
      if (loveList.value.length === 0) return;
      let prevIndex = currentIndex.value - 1;
      if (prevIndex < 0) {
        prevIndex = loveList.value.length - 1;
      }
      playSongByIndex(prevIndex);
      setTimeout(() => {
        isThrottled.value = false;
      }, 1000);
    };

