    const songEnded = () => {
      nextSongzhidong();
    };
    watch(loveList, newList => {
      // 如果当前播放的歌曲被删除
      if (newList.length > 0 && !newList.includes(musicname.value)) {
        // 尝试找到最近的合理索引
        const newIndex = currentIndex.value >= newList.length ? 0 : currentIndex.value;
        musicname.value = newList[newIndex];
        currentIndex.value = newIndex;
      }
      if (newList.length === 0) {
        audioplayer.value.pause();
        isplayer.value = false;
        musicstat.value = null;
        musicname.value = "";
      }
    }, {
      deep: true
    });

    // 获取当前的时间
    const currentTime = ref();
    const timer = setInterval(() => {
      currentTime.value = formatDateTime();
    }, 1000);
