// src/store/modules/love.js
import { addlove, deletelove, getmylove } from "@/api/music/mylove";

export default {
  namespaced: true,
  state: {
    loveList: [],
  },
  mutations: {
    SET_LOVE_LIST(state, list) {
      state.loveList = list;
    },
    ADD_LOVE_ITEM(state, item) {
      state.loveList.unshift(item);
    },
    REMOVE_LOVE_ITEM(state, itemName) {
      state.loveList = state.loveList.filter((item) => item !== itemName);
    },
  },
  actions: {
    async fetchLoveList({ commit }) {
      const res = await getmylove();
      commit("SET_LOVE_LIST", res.data);
    },
    async addLove({ commit }, name) {
      await addlove({ musicname: name });
      commit("ADD_LOVE_ITEM", name);
    },
    async removeLove({ commit }, name) {
      await deletelove({ deletemusicname: name });
      commit("REMOVE_LOVE_ITEM", name);
    },
  },
};
