import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import LoginPage from "../components/login/login.vue";
import { getUserInfo } from "@/utils/setlocal";
const routes = [
  {
    path: "/",
    name: "home",
    redirect: "/home",
    component: HomeView,
    children: [
      {
        path: "/home",
        name: "homepage",
        component: () => import("@/views/Home/homepage.vue"),
      },
      {
        path: "/search",
        name: "search",
        component: () => import("@/views/Search/searchpage.vue"),
      },
      {
        path: "/musicmenu",
        name: "musicmenu",
        component: () => import("@/views/MusicMenu/musicmenu.vue"),
      },
      {
        path: "/music/collection",
        name: "musiccollection",
        component: () => import("@/views/MusicMenu/musiccollection.vue"),
      },
      {
        path: "/userinfo",
        name: "userinfo",
        component: () => import("@/views/Updateinfo/userinfo.vue"),
      },
    ],
  },
  {
    path: "/login",
    name: "login",
    component: LoginPage,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

const whiteList = ["/login", "/register"];

router.beforeEach((to, from, next) => {
  const token = getUserInfo();
  // console.log(token.token);
  if (whiteList.includes(to.path)) {
    next();
  } else {
    if (token.token) {
      next();
    } else {
      next("/login");
    }
  }
});

export default router;
