import request from "@/utils/request";

// 添加我喜欢的
export const addlove = (data) => {
  const res = request.post("/lovelist/mylove", data);
  return res;
};

// 删除我喜欢的
export const deletelove = (data) => {
  const res = request.delete("/lovelist/deletelovemusic", {
    data,
  });
  return res;
};

// 是否被喜欢

export const isloveok = (params) => {
  const res = request.get("/lovelist/islovemusic", { params });
  return res;
};

// 查询所有喜欢
export const getmylove = (params) => {
  const res = request.get("/lovelist/getmylove", { params });
  return res;
};
